.App {
  text-align: center;
  display: flex;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

body,
html,
#root {
  height: 100%;
  background-color: rgb(250, 246, 240);
}

.todo-form {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: rgb(179, 157, 142);
  border-color: rgb(216, 203, 186);
  border-style: solid;
  border-radius: 10px;
}

li {
  text-align: initial;
  background-color: rgb(211, 184, 166);
  padding: 5px;
  margin: 5px;
  border-color: rgb(216, 203, 186);
  border-style: solid;
  border-radius: 15px;
}
